@import 'reset';


* {
	box-sizing: border-box;
}

body {
	// background: #fff;
}


.nav{
	display: none;
	height: 0;
	z-index: 4;

	ul{
		
		li{
			text-align: center;
			margin-bottom: 12%;
			margin-top: 4%;
			@media screen and (min-width: 768px){
				margin-top: 67px;
   				margin-bottom: 0%;
			}
			

			a{
				text-decoration: none;
				text-transform: uppercase;
				letter-spacing: 0.08em;
				color: #565583;
				font-family: 'Cabin Sketch', cursive;
				transition: .4s ease-in-out;
				font-size: 2em;

				@media screen and (min-width: 768px){
					font-size: 2.9em;
				}
				@media screen and (min-width: 1024px){
					font-size: 3.3em;
				}

				&:hover{
					color: #fff;
				}
			}
		}
	}

	&.open{
		display: flex;
		height: 100vh;
		justify-content: center;
		flex-direction: column;
		margin: 0px auto;
		background-color: #d8ddef;
	}
}

.hero-section{
	width: 100%;
	height: 100vh;
	overflow: hidden;
	margin-bottom: 20%;

	.topLine{
		width: 479%;
		display: flex;
		flex-direction: row;

		@media screen and (min-width: 600px){
			width: 252%;
		}
		@media screen and (min-width: 768px){
			width: 198.194444%;
		}
		@media screen and (min-width: 1024px){
			width: 140.194444%;
		}
		@media screen and (min-width: 1224px){
			width: 112.194444%;
		}
		@media screen and (min-width: 1440px){
			width: 83.194444%;
		}
		@media screen and (min-width: 1480px){
			width: 79.194444%;
		}
		

		div:first-child{
			background-image: url('../img/purpleTriLeft.png');
			background-repeat: no-repeat;
			background-size: contain;
			height: 0;
			left: 0;
			top: 0;

			padding-bottom: 13%;
    		width: 13%;
			@media screen and (min-width: 600px){
				padding-bottom: 23%;
				width: 36%;
			}
			@media screen and (min-width: 768px){
				padding-bottom: 15%;
				width: 23.013889%;
			}
			@media screen and (min-width: 1024px){
				width: 22.013889%;
			}
			@media screen and (min-width: 1224px){
				width: 26.013889%;
			}
			@media screen and (min-width: 1440px){
				padding-bottom: 20%;
				width: 35.958333%;
			}

		}

		div:nth-child(2){
			background-image: url('../img/topLogo.png');
			background-repeat: no-repeat;
			background-size: contain;
			height: 0;
			top: 0;

			padding-bottom: 11%;
    		width: 57%;
			@media screen and (min-width: 600px){
				padding-bottom: 23%;
    			width: 132%;
			}
			@media screen and (min-width: 768px){
				padding-bottom: 21%;
				width: 52.013889%;
			}
			@media screen and (min-width: 1024px){
				padding-bottom: 23%;
    			width: 30.013889%;
			}
			@media screen and (min-width: 1224px){
				padding-bottom: 24%;
				width: 44.013889%;
			}
			@media screen and (min-width: 1440px){
				padding-bottom: 28%;
				width: 47.013889%;
			}
		}
	
		.burger{
			z-index: 6;
			position: absolute;
			right: 176px;
			top: 60px;
			cursor: pointer;
	
			div:first-child{
				position: absolute;
				background-color: #825aa6;
				height: 7px;
				width: 67px;
				left: 64px;
				border-radius: 20px;
				transition: .4s ease-in-out;
			}
	
			div:last-child{
				top: 22px;
				position: absolute;
				background-color: #93a1d2;
				height: 7px;
				width: 45px;
				left: 86px;
				border-radius: 20px;
				transition: .4s ease-in-out;
			}

			&.open{
				
				div:first-child{
					width: 45px;
					transform: translateX(20px) rotate(-45deg);
					transition: .4s ease-in-out;
				}
				
				div:last-child{
					transition: .4s ease-in-out;
					transform: translateY(-23px) rotate(45deg);
				}

				.nav{
					display: flex;
				}

			}
		}
	}
	

	
	.middleHero{
		display:flex;
		flex-direction: row;

		.heroText{
			color: #504099;
			text-align: center;
			margin: 0 auto;
			width: 85%;
			@media screen and (min-width: 600px){
				width: 80%;
			}
			@media screen and (min-width: 768px){
				width: 61.97916667%;
			}
			@media screen and (min-width: 1440px){
				margin-left: 17.097222%;
				margin-right: 57.98611111%;
				width: 33.05555556%;
			}
			
			h1{
				@media screen and (max-width: 767px){
					font-size: 3.2em;
				}
				font-weight: 300;
				font-size: 4em;
				text-transform: uppercase;
				letter-spacing: 0.08em;
				font-family: 'Cabin Sketch', cursive;
				white-space: nowrap;
			}
	
			h3{
				font-family: 'Crimson Text', serif;
				font-size: 1.6em;
				margin-top: 21px;
				letter-spacing: 0.08em;
			}
	
			.line{
				margin: 36px auto;
				background-color: #504099;
				height: 2px;
				width: 40%;
				position: relative;
	
				div{ // dot
					background-color: #825aa6;
					height: O;
					width: 5%;
					padding-bottom: 5%;
					position: absolute;
					border-radius: 90%;
					margin: -4px 48% 0 48%;
				}
				
			}
	
			h4{
				text-transform: uppercase;
				letter-spacing: 0.09em;
				font-family: 'Cabin Sketch', cursive;
			}
	
			.down{
				cursor: pointer;
				width: 0; 
				height: 0; 
				border-left: 20px solid transparent;
				border-right: 20px solid transparent;
				border-top: 20px solid #825aa6;
				margin: 20px auto;

				&:hover{
					border-top: 20px solid #d8ddef;
				}
			}
		}


		.rtTri{
			z-index: -1;
			@media screen and (max-width: 1223px){
				display: none;
			}
			@media screen and (min-width: 1224px){
				display: flex;
				background-image: url('../img/purpleTriRight.png');
				background-repeat: no-repeat;
				background-size: contain;
				height: 0;
				padding-bottom: 40%;
				width: 17.694444%;
				right: 0;
				position: absolute;
				margin-right: 0px;
				top: 173px;
			}

		}
	}
	
	

	.bottomLine{
		display: flex;
		flex-direction: row;
		position: relative;
		width: 100%;

		div:first-child{
			background-image: url('../img/leftLogo.png');
			background-repeat: no-repeat;
			background-size: contain;
			height: 0;
			left:0;
			bottom: 0;

			display: none;
			@media screen and (min-width: 385px){
				display: block;
				padding-bottom: 185.597222%;
				width: 68%;
				margin-left: -157px;
				margin-top: -288px;
			}
			@media screen and (min-width: 600px){
				margin-top: -276px;
				padding-bottom: 105%;
				width: 43.652778%;
			}
			@media screen and (min-width: 768px){
				margin-top: -46px;
				padding-bottom: 41%;
				width: 1.652778%;
			}
			@media screen and (min-width: 1024px){
				padding-bottom: 46%;
				margin-left: -600px;
			}
			@media screen and (min-width: 1440px){
				padding-bottom: 51%;
				width: 50.652778%;
   				margin-left: 0px;
			}
		}

		div:nth-child(2){
			background-color: #d2c3df;
			height: 4px;
			transform: rotate(-48deg);
			position: absolute;

			display: none;
			@media screen and (min-width: 385px){
				display: block;
				width: 42.375%;
				bottom: 465px;
				right: -53px;
			}
			@media screen and (min-width: 599px){
				bottom: 225px;
				right: -39px;
			}
			@media screen and (min-width: 600px){
				width: 42.375%;
				bottom: 400px;
				right: -84px;
			}
			@media screen and (min-width: 768px){
				width: 72.375%;
				bottom: 353px;
				left: 50%;
			}
			@media screen and (min-width: 1024px){
				width: 53.375%;
				bottom: 426px;
				left: 64%;
			}
			@media screen and (min-width: 1224px){
				width: 30%;
				left: 47%;
			}
			@media screen and (min-width: 1440px){
				left: 24%;
				width: 44%;
			}
		}

		div:nth-child(3){
			background-color: #d2c3df;
			height: 4px;
			transform: rotate(-48deg);
			position: absolute;

			display: none;
			@media screen and (min-width: 385px){
				display: block;
				width: 66.375%;
				bottom: 424px;
				right: -31%;
			}
			@media screen and (min-width:599px){
				bottom: 349px;
			}
			@media screen and (min-width: 600px){
				width: 63.375%;
				bottom: 329px;
				right: -29%;
			}
			@media screen and (min-width: 768px){
				width: 59.375%;
				bottom: 204px;
				left: 52%;
			}
			@media screen and (min-width: 1024px){
				left: 42%;
				width: 36.375%;
			}
			@media screen and (min-width: 1224px){
				bottom: 426px;
				left: 50%;
			}
			@media screen and (min-width: 1440px){
				bottom: 393px;
    			left: 19%;
			}
			
		}

		div:last-child{
			@media screen and (max-width: 767px){
				display: none;
			}
			background-image: url('../img/bottomLogo.png');
			background-repeat: no-repeat;
			background-size: contain;
			height: 0;
			padding-bottom: 45%;
			width: 154.013889%;
			@media screen and (min-width: 768px){
				display: flex;
				margin-top: -119px;
			}
			@media screen and (min-width: 1024px){
				margin-left: 31.333333%;
    			margin-top: 0px;
			}
			@media screen and (min-width: 1440px){
				margin-top: -30px;
				margin-left: 36%;
			}
				
		}
	}

}


