// ALL h2s 
h2{
	text-align: center;
	text-transform: uppercase;
	letter-spacing: 0.14em;
	color: #541b8e;
	font-family: 'Sniglet', cursive;
	font-size: 2em;
	@media screen and (min-width: 768px){
		font-size: 2.4em;
	}
}






